  padding-left: 0;
  padding-right: 0;
} */
#app .no-pd{
    padding-left: 0;
    padding-right: 0;
}
#app button:focus{
  outline: none;
}
img{
  width: 100%;
}
.ui-wrp-start .ui-slider-box {
  position: fixed;
  top:0;
  z-index: 99;
}
.ui-wrp-start .ui-cont-box {
  padding-top: 48px;
}
@media (min-width: 992px) {
.ui-wrp-start .ui-slider-box {
    position: relative;
}
.ui-wrp-start .ui-cont-box {
    padding-top: 0;
}
}




