
.ui-slider-bei{
    color: rgba(255, 255, 255, 0.5);
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #343a40;
    text-align: center;
    line-height: 35px;
}
.ui-slider-lang {
    color: rgba(255, 255, 255, 0.5);
}
.ui-slider-lang {
    cursor: pointer;
    margin-bottom: 10px;
    position: absolute;
    right: 80px;
    top: 14px;
}
.h{
    height: 100%;
}
.h-ui-auto {
    height: auto;
}
.ui-item-txt {
    padding: 22px 54px;
}
.ui-item-des{
    color: #9B9B9B;
    line-height: 30px;
}
.navbar-light .navbar-nav .nav-link{
    color: #333;
}
.ui-slider-lang span:hover,
.navbar-dark .navbar-nav .nav-link:hover,
.navbar-dark .navbar-nav .nav-link:focus,
.navbar-dark .navbar-nav .nav-link:visited{
    color: rgba(255, 255, 255, 0.75);
    font-weight: bold;
}
@-webkit-keyframes fadeInOut2 {
0% {
        opacity:0.8;
}
100% {
        opacity:1;
}
}
.anim_fade_image {
    -webkit-animation-name: fadeInOut2;
    -webkit-animation-timing-function: ease-in-out;
    -webkit-animation-iteration-count: 1;
    -webkit-animation-duration: 0.8s;
}
div .swiper-button-next,
div .swiper-button-prev{
    background-size: 16px 44px;
}
div .swiper-button-next:focus,
div .swiper-button-prev:focus{
    outline: none;
}
@media (min-width: 768px) {
}
/* 中等屏幕（桌面显示器，大于等于 992px） */
@media (min-width:  992px) {
.h-ui-auto{
        height: 100%;
        overflow: hidden;
}
.move-pl {
        padding-left: 0;
}
.move-pr{
        padding-right: 0;
}
.ui-nav-logo {
        margin-bottom: 98px;
}
.ui-nav-box .navbar-expand-lg .navbar-nav {
        -ms-flex-direction: column;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
                flex-direction: column;
}
.ui-nav-box .navbar-expand-lg {
        -ms-flex-flow: column nowrap;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
                flex-flow: column nowrap;
        text-align: center;
}
.ui-nav-box, .ui-navbar {
        height: 100%;
}
.ui-nav-box .navbar-collapse {
        -webkit-box-align: flex-start;
        -ms-flex-align: start;
            align-items: flex-start;
}
.ui-slider-bei,
    .ui-slider-lang {
         display: block;
}
.ui-slider-lang {
        right: auto;
        top: auto;
        position: inherit;
}
.ui-slider-bei{
        position: inherit;
        width: auto;
        background-color: transparent;
        text-align: center;
        line-height: 1;
}
.ui-item-txt {
        display: none;
}
}




